<template>
  <div :row="row" >
    <Button type="error" v-if="flag" @click="handleCancleFocus">取消互关</Button>
    <Button type="success"  @click="handleFocusOn" v-else>关注Ta</Button>
  </div>
</template>

<script>
  export default {
    name: "bt",
    inject: ['reload'],
    data() {
      return {
        flag:false,
      }
    }, methods: {
      checkFocusOr() {
        this.flag=false
        this.axios.get('fansCheck', {params: {focusUserid: this.row.focusBeuserid, focusBeuserid: this.row.focusUserid}})
          .then(res => {
            if (res.data.code === 100) {
              // console.log("你也是他的粉丝")
              this.flag = true
            }
          }).catch(error => {
          this.$Message.warning("检查是否关注出错")
        })
      },handleCancleFocus(){
        //取消互关，应该是自己的关注列表去掉这个
        //console.log(this.row.focusBeuserid+"    "+this.row.focusUserid)
        this.axios.delete('focus',{params:{focusUserid:this.row.focusBeuserid,focusBeuserid:this.row.focusUserid}}).then(response=>{
          if(response.data.code===100){
            this.$Message.success("取消互关")
            this.$store.commit('setFocusNumSub')
            this.reload()
          }else{
            this.$Message.error("取关失败")
          }
        }).catch(error=>{
          this.$Message.warning("取消关注失败")
        })
      },handleFocusOn(){
        this.axios.get('focus',{params:{focusUserid:this.row.focusBeuserid,focusBeuserid:this.row.focusUserid}}).then(res=>{
          // console.log(res.data.code);
          if(res.data.code===100){
            this.$Message.success("互关成功")
            this.$store.commit('setFocusNumAdd')
            this.reload()
          }else{
            this.$Message.error("互关失败")
          }
        }).catch(error=>{
          this.$Message.warning("互关失败")
        })
      }
    }, components: {}, mounted() {
      this.checkFocusOr(this.row)
    }, props: ['row']
  }
</script>

<style scoped>

</style>



